 la Fiscalía General de la Nación. Dado que muchas víctimas sienten temor de denunciar, el sistema judicial permite mecanismos de protección, como la reserva de identidad, protección policial y medidas cautelares para salvaguardar la vida e integridad de la víctima y su familia. En procesos penales por extorsión, se utilizan frecuentemente técnicas especiales de investigación como la interceptación de comunicaciones, seguimientos, agentes encubiertos y análisis de trazabilidad digital (Rincón Ortiz, 2019).")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema6/12.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-left"}},[_vm._v("En cuanto a la pena, el Artículo 244 establece que la extorsión será sancionada con prisión de 12 a 16 años y multa de 600 a 1.200 salarios mínimos legales mensuales vigentes. Estas penas pueden aumentar si concurren agravantes, como las mencionadas anteriormente. Además, los bienes que se utilicen para la comisión del delito o que provengan de él pueden ser objeto de comiso por parte del Estado.")]),_c('div',{staticClass:"bg-color-1 p-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La Corte Suprema de Justicia, en sentencias como la SP10969-2021, ha reiterado que no se exige que la víctima haya entregado el bien para que exista extorsión; basta con que se haya producido la amenaza idónea y el constreñimiento con fines patrimoniales. En otras decisiones, se ha aclarado que cuando la conducta delictiva incluye actos adicionales como violencia física, la conducta puede estar concursando con otros delitos como lesiones personales, secuestro simple o agravado, según el caso.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;retur